/* Experience.css */
.experience-section {
  padding: 4rem 2rem;
  background-color: #f4f4f9;
  text-align: center;
}

.experience-section h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #007bff;
}

.experience-list {
  display: flex;
  justify-content: center;
}

.experience-card {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 60%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
}

.experience-card h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.experience-card h4 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  color: #666;
}

.experience-card p {
  font-size: 1rem;
  color: #666;
}
