/* Add this to your CSS files */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.app {
  animation: fadeIn 2s ease-in;
}

.about {
  padding: 4rem 2rem;
  background-color: #fafafa;
}

.about h1 {
  color: #007bff;
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

.about p {
  font-size: 1.2rem;
  line-height: 1.8;
  max-width: 800px;
  margin: 0 auto;
  color: #444;
}
