/* Projects.css */
.projects {
  padding: 5rem 2rem; /* Increased padding */
  background-color: #fff;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 5rem;
}

.project-card {
  background-color: #e0f7fa; /* Slightly lighter background for contrast */
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.project-card:hover {
  transform: translateY(-10px); /* Add hover effect */
}

.project-card h3 {
  margin-top: 0;
  color: #007bff; /* Enhanced color */
}

.project-card p {
  color: #555;
}

.project-card a {
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
}

.project-card a:hover {
  text-decoration: underline;
}

.projects {
  margin-bottom: 5rem; /* Space between projects and footer */
}
