/* Add this to your CSS files */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.app {
  animation: fadeIn 2s ease-in;
}

.footer {
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
  position: fixed;
  width: 100%;
  bottom: 0;
}

.footer a {
  color: #00d1b2;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}
