/* Skills.css */
.skills-section {
  padding: 4rem 2rem;
  background-color: #f4f4f9;
  text-align: center;
}

.skills-section h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #007bff;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.skill-card {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.skill-card h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.skill-card p {
  font-size: 1rem;
  color: #666;
}
